.navbar-wrap {
  z-index: 99999;
  position: relative;
  margin-bottom: 100px;
}

.navbar {
  background:White;
  position: fixed;
  top: 0;
  left: 0;

  z-index: 1000;

  width: 100%;
  margin-bottom: 100px;
  box-shadow: 0 0 15px rgba(0,0,0,0.2);
  height: 70px;
  > .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
  }
  &-list-item {
    text-decoration: none;
    list-style: None;

  }

  &-profile-img {
    margin-left: 16px;
  }

  &-logo {
    max-height: 50px;
  }
  


  &-list-link {
    color:black;
    text-decoration: none;
    font-weight: 600;
  }
  &-items-list {
    display: flex;
    align-items: center;
    .navbar-list-item:nth-child(2) {
      margin: 0 30px;
    }
  }

  .sticky {
    position: fixed;
    top: 0;
    width: 100%;
  }


  
}